.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
  background-color: #fff;
  color: white;
  padding: 2.5vh 5vw;
}

.main {
  padding-top: 15vh;
  padding-bottom: 19vh;
}

@media screen and (min-width: 768px) {
  .wrapper {
    padding: 2.5vh 9vw;
  }

  .main {
    padding-top: 13vh;
    padding-bottom: 9vh;
  }
}