.wrapper {
  width: 100%;
  max-width: 510px;
  margin: 0 auto;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: -20px;
  will-change: opacity, transform;
  position: relative; 
}

.played {
  animation: mainScale 2s linear 2s forwards;

  .pizza {
    cursor: pointer;

    &:hover {
      transform: scale(1.1);
    }
  }
}

.pizza {
  transition: transform .2s ease-in-out;
  padding: 10px;

  &:nth-child(1) {
    .name {
      color: #269F49;
      text-transform: uppercase;
    }
  }
}

.imageHolder {
  position: relative;
  background-color: #FCC047;
  border-radius: 13px;
  width: 128px;
  height: 128px;
}

.name {
  white-space: nowrap;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
  font-size: 17px;
  color: #7A3C09;
}

@keyframes mainScale {
  0% {
    transform: rotate(0) scale(1);
  }
  55% {
    transform: rotate(360deg) scale(0);
  }
  100% {
    transform: rotate(0) scale(1);
  }
}

@media screen and (min-width: 768px) {
  .pizza {
    padding: 31px;
  }

  .container {
    margin: -31px;
  }

  .wrapper {
    padding-bottom: 13vh;
  }
}
