.btn {
  min-width: 194px;
  padding: 14px;
  font-size: 21px;
  text-align: center;
  color: #FFFFFF;
  background-color: transparent;
  background: #269F49;
  border-radius: 45px;
  border: 0;
  cursor: pointer;
  transition: transform .2s ease-in-out;
  text-transform: uppercase;

  &:hover {
    transform: scale(1.1);
  }
}

.red {
  background-color: #CE352B;
  position: relative;

  &:hover {
    transform: scale(1);
    
    &::before {
      transform: translate(-50%, -50%) scale(1.1);
    }
  }

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(100% + 8px);
    height: calc(100% + 8px);
    border: 2px solid #CE352B;
    border-radius: 65px;
    transition: transform .2s ease-in-out;
  }
}

@media screen and (min-width: 768px) {
  .btn {
    min-width: 294px;
    padding: 20px;
    font-size: 31px;
    border-radius: 65px;
  }
}