.headerWrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;  

  h3 {
    font-size: 20px;
    margin: 0;
    line-height: 1.4;
    text-align: center;
    text-transform: uppercase;
    color: #269F49;

    span {
      cursor: pointer;
      padding: 10px;
    }
  }
}

.btn {
  transition: opacity .2s ease-out, visibility .2s ease-out;
}

.logo {
  max-width: 251px;
  margin-bottom: 20px;
}

.played {
  .btn {
    opacity: 0;
    visibility: hidden;
  }

  button {
    color: #269F49;
  }
}

.selected {
  .btn {
    opacity: 1;
    visibility: visible;
  }

  button {
    color: #fff;
  }
}

@media screen and (min-width: 495px) {
  .headerWrapper {
    justify-content: space-between;
  }

  .logo {
    margin-bottom: 0;
  }
  
  h3 {
    font-size: 31px;
  }
}