.name {
  white-space: nowrap;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
  font-weight: 400;
  font-size: 17px;
  color: #7A3C09;
  background-color: #fff;
  border-radius: 10px;
  padding: 10px;
}

.message {
  color: #000;
  position: relative;
  min-height: 332px;
  overflow: hidden;
}

.imageHolder {
  transition: transform .3s linear;
  transform: scale(2.5);
  transform-origin: top;
}

.content {
  width: 100%;
  text-align: center;
  position: absolute;
  top: 35%;
  text-transform: uppercase;
  z-index: 5;
  opacity: 0;

  h3 {
    font-size: 28px;
    margin: 0 0 15px;
    padding: 0 20px;
  }
  
  h4 {
    color: #7A3C09;
    font-size: 38px;
    margin: 0;
  }
  
}

.messageVisible {
  .content {
    animation: setVisible 1s linear forwards;
  }
}

.textGreen {
  color: #269F49;
}

.textRed {
  color: #CE352B;
}

@keyframes setVisible {
  0% {
    transform: rotate(0) scale(1);
    opacity: 0;
  }
  100% {
    transform: rotate(360deg) scale(1);
    opacity: 1;
  }
}

@media screen and (min-width: 768px) {
  .message {
    min-height: 508px;
  }

  .imageHolder {
    transform: scale(3.9);
  }

  h3 {
    font-size: 64px;
    margin: 0 0 15px;
  }
  
  h4 {
    color: #7A3C09;
    font-size: 48px;
    margin: 0;
  }
}