.winners {
  position: fixed;
  z-index: 10;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  background-color: rgba(0, 0, 0, .1);
}

.visible {
  opacity: 1;
  visibility: visible;
}

.contentHolder {
  width: 100%;
  max-width: 470px;
  background: #fff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(25px);
  border-radius: 25px;
  font-size: 14px;
  color: #000;

  ul {
    text-align: left;
    list-style: none;
    display: flex;
    flex-direction: column-reverse;
  }

  li {
    display: flex;
    align-items: center;
  }

  p {
    color: #269F49;
    font-size: 16px;
    margin-right: 20px;
    min-width: 40%;
  }
}

.button {
  width: 100%;
  background: #CE352B;
  border-radius: 69px;
  color: #fff;
  font-size: 24px;
  padding: 12px;
  border: 0;
  text-transform: capitalize;
  cursor: pointer;
}

.close {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 20px;
  height: 20px;
}

.empty {
  padding: 30px;
}